.pagination li:first-child > a {
  position: relative;
  width: 100%;
  height: 100%;
}

.pagination li:first-child > a:before {
  content: "";
  position: absolute;
  top: 45%;
  left: 0;
  width: 14px;
  height: 14px;
  border-left: 1px solid #13b5f7;
  border-bottom: 1px solid #13b5f7;
  transform: rotate(45deg) translateY(-50%);
  transition: left .2s;
}

.pagination li:first-child > a:hover:before {
  left: -5px;
}

.pagination li:nth-last-child(2) > a {
  position: relative;
  width: 100%;
  height: 100%;
}

.pagination li:nth-last-child(2) > a:before {
  content: "";
  position: absolute;
  top: 45%;
  left: -5px;
  width: 14px;
  height: 14px;
  border-top: 1px solid #13b5f7;
  border-right: 1px solid #13b5f7;
  transform: rotate(45deg) translateY(-50%);
  transition: left .2s;
}

.pagination li:nth-last-child(2) > a:hover:before {
  left: 0px;
}

.pagination li a {
